<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/notice')" class="current">공지</li>
			<li @click="goto('/admin/news')">뉴스</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">
				새 뉴스 등록
			</p>
			<div class="input-container">
				<div class="img-upload-container">
					<div class="input-item align-top">
						<input type="file" id="files" name="file" />
						<AddImageSingle :refName="'mainImage'" @mainImage="setMainImage" />
					</div>
				</div>

				<div class="input-item">
					<p class="small-title">제목</p>
					<input
						type="text"
						class="admin-input large"
						name="title"
						value="프로그램명"
						v-model="title"
					/>
				</div>

				<div class="input-item">
					<p class="small-title">내용</p>
					<CKEditor :contents="contents" ref="editor" />
				</div>

				<button class="outlined-btn" @click="addNews">등록</button>
			</div>
		</div>
	</div>
</template>

<script>
import Store from '@/store';
import AddImageSingle from '@/components/common/AddImageSingle';
import CKEditor from '@/components/common/CKEditor';
import { mapGetters } from 'vuex';
import { addNews } from '@/api/news';

export default {
	components: { AddImageSingle, CKEditor },
	computed: {
		...mapGetters(['main']),
	},
	created() {
		Store.commit('SET_MAIN', null);
	},
	data: () => ({
		title: '',
		contents: '',
	}),
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async addNews() {
			this.contents = this.$refs.editor.getContents();

			if (!this.title) {
				this.errMsg = '제목을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.contents) {
				this.errMsg = '콘텐츠를 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			let data = new FormData();
			if (this.main) {
				data.append('file', this.main);
			}
			data.append('title', this.title);
			data.append('contents', this.contents);
			console.log(this.contents);
			let result;
			try {
				result = await addNews(data);
				if (result.status == 200) {
					alert('뉴스가 등록되었습니다');
				} else {
					alert('에러가 발생했습니다');
				}
			} catch (e) {
				console.log(e);
				alert('에러가 발생했습니다');
			}
			await this.initForm();
		},
		initForm() {
			this.selected = '';
			this.title = '';
			this.contents = '';
			Store.commit('SET_MAIN', '');
		},
		setMainImage(data) {
			Store.commit('SET_MAIN', data);
			console.log(this.main);
		},
	},
};
</script>

