// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchNewsTotalCount() {
	return instance.get(`api/news/totalCount`);
}

function fetchNews(page) {
	return instance.get(`api/news/all/${page}`);
}

function fetchNewsDetail(id) {
	return instance.get(`api/news/detail/${id}`);
}

function addNews(data) {
	return instanceWithAuth.post('api/admin/news/new', data);
}

function updateNews(id, data) {
	return instanceWithAuth.put(`api/admin/news/update/${id}`, data);
}

function deleteNews(data) {
	return instanceWithAuth.delete('api/admin/news/delete', { data });
}

export {
	fetchNewsTotalCount,
	fetchNews,
	fetchNewsDetail,
	addNews,
	updateNews,
	deleteNews,
};
